import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import {Link} from 'react-router-dom';
import {menu} from 'mocks/menu';

// components
import Row from '../grid/Row';
import Col from '../grid/Col';
import Menu from './Menu';
import { CSSTransition } from 'react-transition-group';


class Header extends Component {

	render() {
		const { className, isDark, showMenu, closeMenu, showMenuFunc} = this.props;
		const linkClasses = classNames('header-link magnetize', className, { 'active': showMenu });
		const menuTitle = showMenu ? 'Ð²ÐµÑÐ½ÑÑÑÑÑ' : 'Ð¼ÐµÐ½Ñ';
		const logoImg1 = (showMenu || isDark) ? '/assets/images/cm_logo_1_w.svg' : '/assets/images/cm_logo_1.svg';
		const logoImg2 = (showMenu || isDark) ? '/assets/images/cm_logo_2_w.svg' : '/assets/images/cm_logo_2.svg';
		const logoImg3 = (showMenu || isDark) ? '/assets/images/cm_logo_3_w.svg' : '/assets/images/cm_logo_3.svg';

		return (
			<Fragment>
				<header className={'header'}>
					<Row className="middle-xs between-xs">
						<Col>
							<Link onClick={closeMenu} to="/" className="magnetize div-style" >
								<img src={logoImg1} alt="Cloudmill" className="img-style im-1" />
								<img src={logoImg2} alt="Cloudmill" className="img-style next im-2" />
								<img src={logoImg3} alt="Cloudmill" className="img-style next im-3" />
							</Link>
						</Col>
						<Col>
							<div className={linkClasses} onClick={showMenuFunc}>{menuTitle}</div>
						</Col>
					</Row>
				</header>
				<CSSTransition
					in={showMenu}
					timeout={1000}
					classNames="scale"
					unmountOnExit
				>
					<Menu items={menu} ifClicked={closeMenu}/>
				</CSSTransition>
			</Fragment>
		);
	}
}

Header.propTypes = {
	className: PropTypes.string,
	isDark: PropTypes.bool,
	showMenu: PropTypes.bool,
	showMenuFunc: PropTypes.func,
	closeMenu: PropTypes.func
};

export default Header;
